@import '../../../../../styles/templarios.abstracts';

:host {
  display: flex;
}

.product-item {
  display: flex;
  flex-direction: column;
  width: 100%;
  overflow: hidden;
  background: var(--tp-color-neutral-1);
  border: 1px solid var(--tp-color-neutral-2);
  border-radius: 8px;

  &__image-container {
    position: relative;
    display: block;
    width: 100%;
    height: 0;
    padding-top: 56.7%;
    overflow: hidden;
  }

  &:hover &__image {
    transform: scale(1.2);
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 300ms ease-in-out;
  }

  &__basic-info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 100%;
    padding: 0.8rem 1.2rem;
  }

  &__prices-container {
    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    align-items: center;
    justify-content: center;
    margin-bottom: 1.2rem;
    text-align: center;
  }
}

::slotted([slot='title']) {
  margin-bottom: 1.2rem;

  a {
    text-decoration: none;
  }
}

::slotted([slot='regular-price']) {
  text-decoration: line-through;
}

::slotted([slot='button']) {
  width: 100%;
  --border-radius: 8px;
}
