import { Component, Host, h, Prop } from '@stencil/core';
import { ProductItem } from './utils/product-item.interface';

@Component({
  tag: 'tp-elem-product-item',
  styleUrl: 'product-item.scss',
  scoped: true,
})
export class TpElemProductItem {
  /**
   * Dados referentes a um produto espec√≠fico.
   */
  @Prop({ reflect: true }) product: ProductItem;

  render() {
    const { regularPrice, onSale, images, permalink } = this.product;

    return (
      <Host>
        <div class="product-item">
          <a href={permalink} class="product-item__image-container">
            <img
              class="product-item__image"
              src={images[0].src}
              alt={images[0].alt}
            />
          </a>

          <div class="product-item__basic-info">
            <slot name="title" />

            <div class="product-item__prices-container">
              {onSale && regularPrice && <slot name="regular-price" />}

              <slot name="price" />
            </div>

            <slot name="button" />
          </div>
        </div>
      </Host>
    );
  }
}
